/**
 * invariant  warning  babel "babel-plugin-dev-expression" 
 *  "https://www.npmjs.com/package/babel-plugin-dev-expression"
 */

export function invariant(cond: boolean, message: string): asserts cond {
  if (!cond) throw new Error(message);
}

export function warning(cond: boolean, message: string) {
  if (!cond) {
    if (typeof console !== "undefined") console.warn(message);

    try {
      // 
      throw new Error(message);
      // eslint-disable-next-line no-empty
    } catch (e) {}
  }
}
