/**
 *  document.caretPositionFromPoint/caretRangeFromPoint 
 * @param x clientX
 * @param y clientY
 * @returns Range
 */
 export function getRangeFromPoint(x: number, y: number): Range | null {
  if ("caretRangeFromPoint" in document) {
    return document.caretRangeFromPoint(x, y)
  }

  if ("caretPositionFromPoint" in document) {
    //@ts-ignore firefox
    let caretPosition = document.caretPositionFromPoint(x, y)
    if (!caretPosition) return null
    let { offsetNode, offset } = caretPosition
    let range = document.createRange()
    range.setStart(offsetNode, offset)
    return range
  }
  if (__DEV__) {
    throw new Error(" document  'caretPositionFromPoint'  'caretRangeFromPoint' ")
  }
  return null
}